import Gotop from "../components/Gotop.vue";
import TopNavOpacity from "../components/TopNavOpacity.vue";
import Footer from "../components/Footer.vue";
import Pavement from "../components/Pavement.vue";
import Logo from "../components/Logo.vue";
import Features from "../components/Features.vue";
import Chat from "../components/Chat.vue";
import { mapActions, mapGetters, mapState, mapMutations } from "vuex";
import Loading from "../components/Loading.vue";

export default {
  data() {
    return {
      address: "",
      phone: "",
      name: "",
      error: "",
    };
  },
  methods: {
    ...mapActions([
      "deleteProduct",
      "addCount",
      "minusCount",
      "confirmOrder",
      "routerToDetail",
    ]),
    ...mapMutations(["setShowNav"]),
    toTop: function () {
      window.scrollTo({
        top: 0,
      });
    },

    order: function () {
      if (this.$store.state.user) {
        if (this.address != "" && this.phone != "" && this.name != "") {
          this.confirmOrder();
        } else {
          this.error = "Please enter all information bellow here.";
        }
      } else {
        alert("You must login first.");
      }
    },
  },
  mounted() {
    this.toTop();
    this.setShowNav(false);
  },
  computed: {
    ...mapState(["userList", "user", "loading"]),
    ...mapGetters(["totalPrice"]),
  },
  components: {
    Gotop,
    TopNavOpacity,
    Footer,
    Pavement,
    Logo,
    Features,
    Loading,
    Chat,
  },
};
